import axios from 'axios';
import { URL_BACKEND } from '../environments/environment';

export const getPaymentSheetByDate = async (fecha) => {
  const rpta = await axios.get(`${URL_BACKEND}/paymentSheet?fecha=${fecha}`)
  return rpta
}

export const postPaymentSheet = async (objPaymentSheet) => {
  const rpta = await axios.post(`${URL_BACKEND}/paymentSheet`,
    JSON.stringify(objPaymentSheet),
    {
      headers: {
        'Content-Type': 'application/json'
      }
    }
  )
  return rpta;
}

export const putPaymentSheet = async (objPaymentSheet) => {
  const rpta = await axios.put(`${URL_BACKEND}/paymentSheet/${objPaymentSheet.id}`,
  JSON.stringify(objPaymentSheet),
  {headers: { 
    'Content-Type': 'application/json'
  }}
  )
  return rpta;
}
