import {
  Table,
  TableContainer,
  TableHead,
  TableRow,
  TableCell,
  TextField,
  TableBody,
  Button,
} from "@material-ui/core";
import React, { useState, useEffect, useContext } from "react";
import { useRef } from "react";
import ReactToPrint from "react-to-print";
import DateContext from "../../context/DateContext";
import useClientes from "../../hooks/useClientes";
import { getPaymentSheetByDate, postPaymentSheet, putPaymentSheet } from "../../services/paymentSheetService";
import { useStyles } from "./styles";

const hojaPagosVacia = {
  importePerla: "",
  totalPagos: "",
  pagosFalto: "",
  saldoAnterior: "",
  pollosMuertos: "",
  saldoActual: ""
}

const HojaPagos = () => {
  const { clientes } = useClientes('payment');
  const [pagos, setPagos] = useState([]);
  const [hojaPagos, setHojaPagos] = useState({ ...hojaPagosVacia });
  const classes = useStyles();
  const hojaRef = useRef();
  const { fecha } = useContext(DateContext);

  // const handleChangePago2 = (i, e) => {
  //   const pagosCopy = [...pagos];
  //   const hojaPagosCopy = { ...hojaPagos };
  //   const nuevoValor = leerPago(e.target.value)
  //   let aux = nuevoValor
  //   if (hojaPagos.totalPagos !== "") {
  //     aux += parseFloat(hojaPagos.totalPagos)
  //   }
  //   if (pagosCopy[i]) {
  //     aux -= pagos[i].total
  //   }
  //   if (aux >= 0) {
  //     pagosCopy[i] = {
  //       pagoString: e.target.value,
  //       total: nuevoValor
  //     }
  //     hojaPagosCopy.totalPagos = aux;
  //     hojaPagosCopy.saldoActual = calcularSaldo(hojaPagosCopy);
  //     setPagos(pagosCopy)
  //     setHojaPagos(hojaPagosCopy);
  //     localStorage.setItem("pagos", JSON.stringify(pagosCopy));
  //     localStorage.setItem("hojaPagos", JSON.stringify({ ...hojaPagos, totalPagos: aux }));
  //   } else {
  //     e.target.value = pagos[i]?.pagoString || 0
  //   }
  // }

  const handleChangePago = (i, e) => {
    const pagosCopy = [...pagos];
    const nuevoTotalPago = leerPago(e.target.value)
    pagosCopy[i] = {
      pagoString: e.target.value,
      total: nuevoTotalPago
    }
    localStorage.setItem("pagos", JSON.stringify(pagosCopy));
    setPagos(pagosCopy)
  }

  const handleChange = (e) => {
    const hojaPagosCopy = { ...hojaPagos };
    hojaPagosCopy[e.target.name] = e.target.value;
    hojaPagosCopy.saldoActual = calcularSaldo(hojaPagosCopy)
    localStorage.setItem("hojaPagos", JSON.stringify(hojaPagosCopy));
    localStorage.setItem("saldoAnterior", hojaPagosCopy.saldoActual);
    setHojaPagos(hojaPagosCopy);
  }

  const calcularSaldo = (hojaPagos) => {
    const { totalPagos, pagosFalto, pollosMuertos, saldoAnterior, importePerla } = hojaPagos
    let saldo = 0;
    saldo += (totalPagos === "" || isNaN(totalPagos)) ? 0 : parseFloat(totalPagos)
    saldo += (pagosFalto === "" || isNaN(pagosFalto)) ? 0 : parseFloat(pagosFalto)
    saldo += (pollosMuertos === "" || isNaN(pollosMuertos)) ? 0 : parseFloat(pollosMuertos)
    saldo += (saldoAnterior === "" || isNaN(saldoAnterior)) ? 0 : parseFloat(saldoAnterior)
    saldo -= (importePerla === "" || isNaN(importePerla)) ? 0 : parseFloat(importePerla)
    return saldo.toFixed(2)
  }

  const leerPago = (pagoString) => {
    let total = 0;
    let arrayPago = pagoString.trim().toLowerCase().split("+");
    arrayPago.forEach((pago) => {
      total += pago !== "" && parseFloat(pago)
    });
    return total;
  }

  const limpiarHoja = () => {
    localStorage.removeItem("pagos");
    localStorage.removeItem("hojaPagos");
    if (localStorage.getItem("saldoAnterior") && localStorage.getItem("saldoAnterior") !== "0") {
      const hojaPagosCopy = { ...hojaPagosVacia };
      hojaPagosCopy.saldoAnterior = parseFloat(localStorage.getItem("saldoAnterior"))
      localStorage.setItem("hojaPagos", JSON.stringify(hojaPagosCopy));
      setHojaPagos(hojaPagosCopy);
    } else {
      setHojaPagos({ ...hojaPagosVacia })
    }
    setPagos([])
  }

  const onlineSave = () => {
    const objPaymentSheet = {
      fecha: fecha,
      hojaPagos: hojaPagos,
      pagos: pagos
    }
    getPaymentSheetByDate(fecha).then((res) => {
      console.log(res);
      if (res.data.length === 0) {
        postPaymentSheet(objPaymentSheet)
      } else {
        putPaymentSheet({ ...objPaymentSheet, id: res.data[0].id })
      }
    })
  }

  useEffect(() => {
    if (fecha !== "aaaa-mm-dd") {
      getPaymentSheetByDate(fecha).then((res) => {
        let pagosTemp = [];
        let hojaPagosTemp = {...hojaPagosVacia};
        if (res.data.length !== 0) {
          pagosTemp = res.data[0].pagos;
          hojaPagosTemp = res.data[0].hojaPagos;
        } else {
          if (localStorage.getItem("pagos")) {
            pagosTemp = [...JSON.parse(localStorage.getItem("pagos"))];
          }
          if (localStorage.getItem("hojaPagos")) {
            hojaPagosTemp = JSON.parse(localStorage.getItem("hojaPagos"));
          }
        }
        const yesterday = new Date(fecha);
        yesterday.setDate(yesterday.getDate() - 1)
        getPaymentSheetByDate(yesterday.toISOString().split("T")[0]).then((resYesterday) => {
          if (resYesterday.data.length !== 0) {
            hojaPagosTemp.saldoAnterior = resYesterday.data[0].hojaPagos.saldoActual;
            hojaPagosTemp.saldoActual = calcularSaldo(hojaPagosTemp);
          }
          setPagos([...pagosTemp]);
          setHojaPagos(hojaPagosTemp);
        }).catch(() => {
          setPagos([...pagosTemp]);
          setHojaPagos(hojaPagosTemp);
        })
      }).catch((e) => {
        console.log(e);
        let pagosTemp = [];
        let hojaPagosTemp = {...hojaPagosVacia};
        if (localStorage.getItem("pagos")) {
          pagosTemp = [...JSON.parse(localStorage.getItem("pagos"))];
        }
        if (localStorage.getItem("hojaPagos")) {
          hojaPagosTemp = JSON.parse(localStorage.getItem("hojaPagos"));
        }
        setHojaPagos(hojaPagosTemp);
        setPagos([...pagosTemp]);
      })
    }
  }, [fecha]);

  useEffect(() => {
    const hojaPagosCopy = {...hojaPagos}
    hojaPagosCopy.totalPagos = pagos.reduce((acc, pago) => acc + pago?.total || 0, 0).toFixed(2);
    hojaPagosCopy.saldoActual = calcularSaldo(hojaPagosCopy);
    localStorage.setItem("hojaPagos", JSON.stringify(hojaPagosCopy));
    setHojaPagos(hojaPagosCopy);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [pagos])

  return (
    <div>
      <div className={classes.containerButtons}>
        <Button variant="contained" color="primary" onClick={limpiarHoja}>
          Limpiar Hoja
        </Button>
        <ReactToPrint
          trigger={() => (
            <Button variant="contained" color="primary">
              Imprimir
            </Button>
          )}
          content={() => hojaRef.current}
        />
        <Button variant="contained" color="primary" onClick={onlineSave}>
          Guardar online
        </Button>
      </div>
      <TableContainer ref={hojaRef} className={classes.table}>
        <Table size="small" aria-label="a dense table">
          <TableHead>
            <TableRow>
              {["Clientes", "Pagos"].map((cellHead) => (
                <TableCell key={cellHead}>{cellHead}</TableCell>
              ))}
            </TableRow>
          </TableHead>
          <TableBody>
            {clientes.map((objCliente, i) => (
              <TableRow key={i} className={classes[`row${i % 4}`]}>
                <TableCell className={classes.bodyCell}>
                  {objCliente.nombre}
                </TableCell>
                <TableCell className={classes.inputCell}>
                  <TextField
                    size="small"
                    variant="filled"
                    value={pagos[i]?.pagoString ? pagos[i]?.pagoString : ""}
                    onChange={(e) => {
                      handleChangePago(i, e);
                    }}
                  />
                </TableCell>
              </TableRow>
            ))}
            <TableRow>
              <TableCell className={classes.bodyCell}>
                TOTAL PAGOS
              </TableCell>
              <TableCell className={classes.inputCell}>
                <TextField
                  size="small"
                  variant="filled"
                  value={hojaPagos.totalPagos ? hojaPagos.totalPagos : ""}
                  disabled
                />
              </TableCell>
            </TableRow>
            <TableRow>
              <TableCell className={classes.bodyCell}>
                PAGOS FALT
              </TableCell>
              <TableCell className={classes.inputCell}>
                <TextField
                  size="small"
                  variant="filled"
                  name="pagosFalto"
                  value={hojaPagos.pagosFalto}
                  onChange={handleChange}
                />
              </TableCell>
            </TableRow>
            <TableRow>
              <TableCell className={classes.bodyCell}>
                POLLOS MUERTOS
              </TableCell>
              <TableCell className={classes.inputCell}>
                <TextField
                  size="small"
                  variant="filled"
                  name="pollosMuertos"
                  value={hojaPagos.pollosMuertos}
                  onChange={handleChange}
                />
              </TableCell>
            </TableRow>
            <TableRow>
              <TableCell className={classes.bodyCell}>
                SALDO ANTERIOR PERLA
              </TableCell>
              <TableCell className={classes.inputCell}>
                <TextField
                  size="small"
                  variant="filled"
                  name="saldoAnterior"
                  value={hojaPagos.saldoAnterior}
                  onChange={handleChange}
                />
              </TableCell>
            </TableRow>
            <TableRow>
              <TableCell className={classes.bodyCell}>
                CUENTA DEL DIA
              </TableCell>
              <TableCell className={classes.inputCell}>
                <TextField
                  size="small"
                  variant="filled"
                  name="importePerla"
                  value={hojaPagos.importePerla}
                  onChange={handleChange}
                />
              </TableCell>
            </TableRow>
            <TableRow>
              <TableCell className={classes.bodyCell}>
                SALDO ACTUAL
              </TableCell>
              <TableCell className={classes.inputCell}>
                <TextField
                  size="small"
                  variant="filled"
                  name="saldoActual"
                  value={hojaPagos.saldoActual}
                  disabled
                />
              </TableCell>
            </TableRow>
          </TableBody>
        </Table>
      </TableContainer>
    </div>
  );
};

export default HojaPagos;
